@use 'base/variables' as v;
@use 'base/mixins' as m;

.perfil {
    margin-top: 3rem;
    @include m.tablet { 
        max-width: 75rem;    
    }
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;   
        }
    }
    &__img {
        border-radius: 50%;
        height: 16rem;
    }
    &__informacion {
        margin-top: v.$normal;
        @include m.tablet { 
            margin-top: 0;    
        }
    }
    &__flex {
        // comentt
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;
        @include m.tablet { 
            padding-right: 20rem;
            flex-direction: row;
            justify-content: space-between;
            gap: 2rem;    
        }
    }
    &__heading {
        font-size: 3rem;
        font-weight: 400;
        color: v.$oscuro;
        
    }
    &__edicion {
        @include m.boton(v.$fondo, v.$oscuro);
        text-transform: unset;
        font-size: 1.5rem;
        padding: .5rem;
        border: 1px solid v.$gris;
        border-radius: .5rem;
        margin: 0;
    }
    &__configuracion {
        color: v.$oscuro;
        font-size: 2.2rem;
    }
}


