@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: flex;
    margin-top: 3rem;
    gap: v.$normal;
    @include m.tablet { 
        margin-top: 0;
        gap: unset;
    }

    &__link {
        font-size: 2.2rem;
        color: v.$oscuro;
        @include m.tablet { 
            margin-right: 2.5rem;
        }
    }
}