@use 'base/variables' as v;
@use 'base/mixins' as m;

.datos {
    margin: 3.5rem 0;
    @include m.telefono { 
        @include m.grid(3, v.$normal);    
    }
    @include m.tablet { 
        margin: 0;
    }
    &__bold {   
        @include m.boldYnormal;
        text-align: center;

        @include m.tablet { 
            text-align: unset;
        }
    }
}

