@use 'base/variables' as v;
@use 'base/mixins' as m;

.usuario {

    &__apodo {
        font-size: 1.6rem;
        font-weight: 700;
        text-align: center;
        @include m.tablet { 
            text-align: unset;    
        }
    }
    &__ciudad {
        font-size: 1.6rem;
        color: darken(v.$gris, 35%);
        text-align: center;
        @include m.tablet { 
            text-align: unset;    
        }
    }
}

