@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 2rem 2rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2.2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    transition: background-color;
    transition-duration: 300ms;

    @include tablet {
        width: auto;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin buscador {
    padding: 1rem 0;
    padding-left: 2.5rem;
    border-radius: 1rem;
    color: v.$oscuro;
    border: none;
    background-color: v.$gris;
    width: 100%;
    display: block;

    @include tablet { 
        width: 30rem;
        height: 4rem;
    }

}
     
@mixin boldYnormal {
    font-weight: 700;
    font-size: 1.5rem;

    span {
        font-weight: 400;
        margin-left: .8rem;
    }
}

@mixin normalYbold {
    font-weight: 400;
    font-size: 1.5rem;
    
    span {
        font-weight: 700;
    }
}
