@use 'base/variables' as v;
@use 'base/mixins' as m; 

.publicacion {
    
    &__imagen {
        width: 100%;
    }
    &__contenido {
        background-color: v.$blanco;
        border: 1px solid darken(v.$gris, 15%);
        padding: v.$normal;

        @include m.tablet { 
            display: none;
        }
    }
    &__flex {
        display: flex;
        justify-content: space-between;
    }
    &__icono {
        color: v.$oscuro;
        font-size: 2rem;
        margin-right: 1rem;
    }
    &__like {
        display: flex;
        align-items: center;
    }
    &__like-imagen {
        border-radius: 50%;
        height: v.$normal;
        width: v.$normal;
    }
    &__like-personas {
        @include m.normalYbold;
        margin-left: 1rem;
    }
    &__menciones {
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    &__usuario,
    &__comentarios,
    &__tiempo {
        margin: 0;
    }
    &__usuario {
        @include m.boldYnormal;
    }
    &__comentarios,
    &__tiempo {
        color: darken(v.$gris, 25%);
        font-size: 1.4rem;
    }
    &__tiempo {
        text-transform: uppercase;
    }
}      
