@use 'base/variables' as v;
@use 'base/mixins' as m; 

.main {
    margin-top: v.$separacion;
    max-width: 90rem;

    &__secciones {
        border-top: 1.5px solid v.$gris;
        padding: v.$normal 0;
        display: flex;
        flex-direction: column;
        align-items: center; 
        @include m.telefono {  
            flex-direction: row;
            justify-content: center;
            gap: 2rem;
        }
        @include m.desktop {
            gap: 6rem;
            padding: 0;
        }
    }
    &__nombres {
        font-size: 1.2rem;
        text-transform: uppercase;
        color: darken(v.$gris, 20%);
        font-weight: 700;
        letter-spacing: 1.5px;
        
        
        &--border {
            color: v.$negro;
            @include m.desktop { 
                &::after {
                    content: '';
                    display: block;
                    border-top: 1px solid v.$oscuro;
                    height: 1rem;
                    width: 11.5rem;
                    margin-top: -4.5rem;
                } 
            }
        }
    }
    &__nombres--pt2 {
        @include m.desktop { 
            padding-top: v.$normal;
        }
    }
    &__grid {
        @include m.grid(1, v.$normal);
        @include m.telefono { 
            @include m.grid(2, v.$normal);    
        }
        @include m.tablet { 
            @include m.grid(3, 2.5rem);   
        }
    }
}





