@use 'base/variables' as v;
@use 'base/mixins' as m; 

.footer {
    padding-top: v.$separacion;

    &__nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        @include m.tablet { 
            flex-direction: row;
            justify-content: center;
            gap: v.$normal;
        }
    }
    &__enlace {
        font-size: 1.2rem;
        color: darken(v.$gris, 25%);
    }
    &__copy {
        text-align: center;
        font-size: 1.2rem;
        color: darken(v.$gris, 25%);
    }
}



