body {
  background-color: #f9f9fd;
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

.m-2 {
  margin-left: 1.5rem;
}

.azul {
  color: #0b0320;
}

.p-2 {
  padding-top: 2rem;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #ffffff;
  padding: 2rem 2rem;
  color: #000000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}

.boton-negro {
  background-color: #000000;
  padding: 2rem 2rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}

.mostrar {
  display: flex !important;
  flex-direction: column;
  align-items: center;
}

.ocultar {
  display: none;
}

.header {
  padding: 2rem 0;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #e9e7e7;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
    align-items: center;
    padding: 1rem 0;
  }
}
.header__heading {
  margin: 0;
  font-size: 2.5rem;
  color: #000000;
  text-align: center;
  font-family: "Dancing Script", cursive;
  font-weight: 700;
}
@media (min-width: 768px) {
  .header__grid {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 5rem;
  }
}
@media (min-width: 1024px) {
  .header__grid {
    margin-right: 20rem;
  }
}
.header__burger {
  margin-top: 2rem;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .header__burger {
    display: none;
  }
}
.header__barra {
  text-align: center;
}
.header__barra--enlace:hover {
  cursor: pointer;
}
.header__nav {
  display: none;
}
@media (min-width: 768px) {
  .header__nav {
    display: block;
  }
}

.navegacion {
  display: flex;
  margin-top: 3rem;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    margin-top: 0;
    gap: unset;
  }
}
.navegacion__link {
  font-size: 2.2rem;
  color: #292c35e1;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-right: 2.5rem;
  }
}

.formulario__buscador {
  padding: 1rem 0;
  padding-left: 2.5rem;
  border-radius: 1rem;
  color: #292c35e1;
  border: none;
  background-color: #e9e7e7;
  width: 100%;
  display: block;
}
@media (min-width: 768px) {
  .formulario__buscador {
    width: 30rem;
    height: 4rem;
  }
}

.perfil {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .perfil {
    max-width: 75rem;
  }
}
.perfil__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .perfil__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.perfil__img {
  border-radius: 50%;
  height: 16rem;
}
.perfil__informacion {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .perfil__informacion {
    margin-top: 0;
  }
}
.perfil__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .perfil__flex {
    padding-right: 20rem;
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
  }
}
.perfil__heading {
  font-size: 3rem;
  font-weight: 400;
  color: #292c35e1;
}
.perfil__edicion {
  background-color: #f9f9fd;
  padding: 2rem 2rem;
  color: #292c35e1;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  text-transform: unset;
  font-size: 1.5rem;
  padding: 0.5rem;
  border: 1px solid #e9e7e7;
  border-radius: 0.5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .perfil__edicion {
    width: auto;
  }
}
.perfil__configuracion {
  color: #292c35e1;
  font-size: 2.2rem;
}

.datos {
  margin: 3.5rem 0;
}
@media (min-width: 480px) {
  .datos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .datos {
    margin: 0;
  }
}
.datos__bold {
  font-weight: 700;
  font-size: 1.5rem;
  text-align: center;
}
.datos__bold span {
  font-weight: 400;
  margin-left: 0.8rem;
}
@media (min-width: 768px) {
  .datos__bold {
    text-align: unset;
  }
}

.usuario__apodo {
  font-size: 1.6rem;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 768px) {
  .usuario__apodo {
    text-align: unset;
  }
}
.usuario__ciudad {
  font-size: 1.6rem;
  color: #948a8a;
  text-align: center;
}
@media (min-width: 768px) {
  .usuario__ciudad {
    text-align: unset;
  }
}

.submenu {
  display: none;
}

.destacada {
  padding-top: 8rem;
}
@media (min-width: 768px) {
  .destacada {
    padding-left: 25rem;
  }
}
.destacada__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 480px) {
  .destacada__grid {
    flex-direction: row;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .destacada__grid {
    align-items: flex-start;
    justify-content: unset;
    gap: 5rem;
  }
}
.destacada__historia {
  transition: transform 0.3s ease;
}
.destacada__historia:hover {
  cursor: pointer;
  transform: scale(1.3);
}
.destacada__imagen {
  border: 2.2px dashed #292c35e1;
  border-radius: 50%;
  height: 8rem;
  width: 8rem;
}
.destacada__informacion {
  font-size: 1.2rem;
  font-weight: 700;
  text-align: center;
}

.main {
  margin-top: 5rem;
  max-width: 90rem;
}
.main__secciones {
  border-top: 1.5px solid #e9e7e7;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 480px) {
  .main__secciones {
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .main__secciones {
    gap: 6rem;
    padding: 0;
  }
}
.main__nombres {
  font-size: 1.2rem;
  text-transform: uppercase;
  color: #b8b2b2;
  font-weight: 700;
  letter-spacing: 1.5px;
}
.main__nombres--border {
  color: #000000;
}
@media (min-width: 1024px) {
  .main__nombres--border::after {
    content: "";
    display: block;
    border-top: 1px solid #292c35e1;
    height: 1rem;
    width: 11.5rem;
    margin-top: -4.5rem;
  }
}
@media (min-width: 1024px) {
  .main__nombres--pt2 {
    padding-top: 2rem;
  }
}
.main__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  .main__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .main__grid {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.publicacion__imagen {
  width: 100%;
}
.publicacion__contenido {
  background-color: #ffffff;
  border: 1px solid #c4bfbf;
  padding: 2rem;
}
@media (min-width: 768px) {
  .publicacion__contenido {
    display: none;
  }
}
.publicacion__flex {
  display: flex;
  justify-content: space-between;
}
.publicacion__icono {
  color: #292c35e1;
  font-size: 2rem;
  margin-right: 1rem;
}
.publicacion__like {
  display: flex;
  align-items: center;
}
.publicacion__like-imagen {
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
}
.publicacion__like-personas {
  font-weight: 400;
  font-size: 1.5rem;
  margin-left: 1rem;
}
.publicacion__like-personas span {
  font-weight: 700;
}
.publicacion__menciones {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.publicacion__usuario, .publicacion__comentarios, .publicacion__tiempo {
  margin: 0;
}
.publicacion__usuario {
  font-weight: 700;
  font-size: 1.5rem;
}
.publicacion__usuario span {
  font-weight: 400;
  margin-left: 0.8rem;
}
.publicacion__comentarios, .publicacion__tiempo {
  color: #aca4a4;
  font-size: 1.4rem;
}
.publicacion__tiempo {
  text-transform: uppercase;
}

.footer {
  padding-top: 5rem;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .footer__nav {
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
  }
}
.footer__enlace {
  font-size: 1.2rem;
  color: #aca4a4;
}
.footer__copy {
  text-align: center;
  font-size: 1.2rem;
  color: #aca4a4;
}