@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: v.$normal 0;
    background-color: v.$blanco;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid v.$gris;    
    
    @include m.tablet { 
       flex-direction: row; 
       justify-content:space-between;
       gap: v.$normal;
       align-items: center;
       padding: 1rem 0;
    }

    &__heading {
        margin: 0;
        font-size: 2.5rem;
        color: v.$negro;
        text-align: center;
        font-family: v.$fuenteCursiva;
        font-weight: 700;
    }
    &__grid {
        @include m.tablet { 
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            gap: v.$separacion;
        }
        @include m.desktop { 
            margin-right: calc(v.$separacion + 15rem);
        }
    }

    // Estilos a la barra
    &__burger {
        margin-top: v.$normal;
        font-size: 2.5rem;

        @include m.tablet { 
            display: none;    
        }
    }
    &__barra {
        text-align: center;
    }
    &__barra--enlace {

        &:hover {
            cursor: pointer;
        }
    }

    &__nav {
        display: none;

        @include m.tablet { 
            display: block;    
        }
    }
}



