@use 'base/variables' as v;
@use 'base/mixins' as m;

.destacada {
    padding-top: calc(v.$separacion + 3rem); 
    @include m.tablet { 
        padding-left: 25rem;    
    }
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: v.$normal;

        @include m.telefono { 
            flex-direction: row;
            justify-content: center;   
        }
        @include m.tablet { 
            align-items: flex-start;
            justify-content: unset;
            gap: v.$separacion;
        }
    }
    &__historia {
        transition: transform .3s ease;
        &:hover {
            cursor: pointer;
            transform: scale(1.3);
        }
    }
    &__imagen {
        border: 2.2px dashed  v.$oscuro;
        border-radius: 50%;
        height: 8rem;
        width: 8rem;
    }
    &__informacion {
        font-size: 1.2rem;
        font-weight: 700;
        text-align: center;
    }
}